"use client";

import {
  createContext,
  Dispatch,
  ReactNode,
  RefObject,
  SetStateAction,
  useContext,
  useRef,
  useState,
} from "react";
import { Socket } from "socket.io-client";
import { ClientToServerEvents, ServerToClientEvents } from "./types/game";

interface AssetsType {
  arrowImg: boolean;
}

type SocketType = Socket<ServerToClientEvents, ClientToServerEvents>;

interface ContextType {
  assets: AssetsType;
  setAssets: Dispatch<SetStateAction<AssetsType>>;
  isAllAssetsLoaded: boolean;
  SocketRef: RefObject<SocketType | null>;
}

const Context = createContext<ContextType | undefined>(undefined);

export const Provider = ({ children }: { children: ReactNode }) => {
  const [assets, setAssets] = useState<AssetsType>({
    arrowImg: false,
  });
  const isAllAssetsLoaded = Object.values(assets).every((asset) => asset);

  const SocketRef = useRef<SocketType>(null);

  return (
    <Context.Provider
      value={{ assets, setAssets, isAllAssetsLoaded, SocketRef }}
    >
      {children}
    </Context.Provider>
  );
};

export const useGlobalContext = () => {
  const context = useContext(Context);
  if (!context)
    throw new Error("useGlobalContext must be used within a Provider");
  return context;
};
